

































































































































.answer-wrap {
	position: relative;
	width: 100%;
}

.answer {
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	padding: 1rem;
	text-align: center;
	border: 1px solid #333333;
	box-shadow: 0 3px 8px rgba(0, 0, 0, 0.15);

	@media (max-width: 1246px) {
		height: auto;
	}

	p {
		margin: 0;
	}

	&:hover {
		cursor: pointer;
	}
}

