





























































.wrap {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}

.node {
	position: relative;
	box-sizing: content-box;
	display: flex;
	justify-content: center;
	width: 300px;
	padding: 2rem 0 2rem;

	&__body {
		width: 100%;
		padding: 1rem;
		color: white;
		text-align: center;
		box-shadow: 0 3px 8px rgba(0, 0, 0, 0.15);

		p {
			margin: 0;
		}
	}
}
